@import '../../index.scss';

.search {
  @include flexbox();
  @include justify-content(center);
  padding: 3rem;

  @media (max-width: 650px) {
    @include flex-direction(column);
    padding: 2rem;
  }

  &__input {
    background-color: transparent;
    outline: none;
    border: none;
    border-bottom: 1px solid $green;

    width: 100%;

    font-size: 2.4rem;
    color: $purple;
    padding: 0.5rem;
    margin: 0 4.4rem;

    transition: all 400ms ease;

    &:focus {
      border-bottom: 1px solid $pink;
    }

    @media (max-width: 650px) {
      text-align: center;
      margin: 2rem 0;
    }
  }

  &__btn {
    cursor: pointer;
    outline: none;
    background-color: $green;
    border: 1px solid $green;

    width: 27rem;

    font-size: 2.4rem;
    color: #fff;

    padding: 0.5rem;
    margin: 0 1rem;


    transition: all 300ms ease;

    &:hover {
      background-color: transparent;
      color: $green;
      @include box-shadow(0 0 2px $green);
    }

    @media (max-width: 650px) {
      width: 100%;
      margin: 0;
    }
  }
}
