* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: $font-size;
  
  @media (max-width: 650px) {
    font-size: $font-size - 2;
  }
}

body {
  width: 100vw;
  max-width: 100%;

  font-family: $font-family;
  font-size: 1.6rem;
  font-weight: 400;
}

.container {
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
}

.box-shadow {
  @include box-shadow($box-shadow);
}

.flex-center {
  @include flexbox();
  @include justify-content(center);
  @include align-items(center);
}
