@import '../../index.scss';

.card {
  width: 100%;
  margin: 1rem;

  @include flexbox();
  @include flex-direction(row-reverse);

  @media (max-width: 650px) {
    @include flex-direction(column);
    margin: 1rem 0 8rem;
  }

  &__figure {
    @extend .flex-center;
    width: 300px;
    margin: 2rem auto;
  }

  &__main {
    @include flexbox();
    @include flex-direction(column);
    @include justify-content(center);
    @include align-items(center);

    width: 100%;

    &__title {
      font-size: 4rem;
      font-weight: 700;
      color: $purple;

      margin: 1rem 0;
    }

    &__info {
      margin: 1rem 0;

      & small {
        font-size: 1.4rem;
        font-weight: 700;
        color: $green;
        margin: 0 2rem;
      }

      & span {
        text-transform: uppercase;
        letter-spacing: 0.2rem;
      }
    }

    &__overview {
      width: 90%;
      margin: 1rem 0;

      font-size: 1.6rem;
      letter-spacing: 0.05rem;
      line-height: 2.2rem;
    }
  }
}
